// MainView.jsx
const MainView = ({
    layerConfigs,
    onLayerConfigChange,
    isConnected,
    isTokenHolder,
    onSaveConfig,
    isSaving,
    blendModes,
    contextConfigurations
}) => {
    const canvasRefs = {
        1: useRef(null),
        2: useRef(null)
    };
    
    const managerRefs = useRef({});
    const resizeTimeoutRef = useRef(null);
    const baseUrl = import.meta.env.BASE_URL.endsWith('/') 
        ? import.meta.env.BASE_URL 
        : `${import.meta.env.BASE_URL}/`;

    // Check if we're viewing someone else's profile
    const isViewingOtherProfile = window.lukso?.isContext && 
        window.lukso?.context?.address !== window.lukso?.address;

    // Initialize canvas managers
    useEffect(() => {
        console.log('Initializing canvases with configs:', layerConfigs);
        console.log('Is viewing other profile:', isViewingOtherProfile);
        
        Object.keys(canvasRefs).forEach(layerId => {
            const canvas = canvasRefs[layerId].current;
            if (!canvas) return;
            
            managerRefs.current[layerId] = new CanvasManager(canvas, layerId);
            managerRefs.current[layerId].setImage(`${baseUrl}assets/Layer${layerId}.webp`);
        });

        // Start animations with current config
        Object.keys(layerConfigs).forEach(layerId => {
            const manager = managerRefs.current[layerId];
            if (manager) {
                manager.animate(layerConfigs[layerId]);
            }
        });

        const handleResize = () => {
            if (resizeTimeoutRef.current) {
                window.cancelAnimationFrame(resizeTimeoutRef.current);
            }
            
            resizeTimeoutRef.current = window.requestAnimationFrame(() => {
                Object.values(managerRefs.current).forEach(manager => {
                    if (manager) {
                        manager.resize();
                    }
                });
            });
        };

        window.addEventListener('resize', handleResize, { passive: true });

        return () => {
            window.removeEventListener('resize', handleResize);
            if (resizeTimeoutRef.current) {
                window.cancelAnimationFrame(resizeTimeoutRef.current);
            }
            Object.values(managerRefs.current).forEach(manager => {
                if (manager) {
                    manager.stop();
                }
            });
            managerRefs.current = {};
        };
    }, [layerConfigs, isViewingOtherProfile]);

    return (
        <div className="main-view">
            <div id="canvas-container">
                <div className="grid-overlay"></div>
                {[1, 2].map(layerId => (
                    <canvas
                        key={layerId}
                        ref={canvasRefs[layerId]}
                        className="canvas"
                        style={{
                            zIndex: layerId,
                            mixBlendMode: layerConfigs[layerId].blendMode
                        }}
                    />
                ))}
                <img 
                    src={`${baseUrl}assets/Entity.webp`}
                    alt="Entity" 
                    className="entity-logo" 
                />
            </div>

            {/* Only show control panel if we're a token holder AND not viewing someone else's profile */}
            {isTokenHolder && !isViewingOtherProfile && (
                <ControlPanel
                    layerConfigs={layerConfigs}
                    onLayerConfigChange={onLayerConfigChange}
                    isConnected={isConnected}
                    isTokenHolder={isTokenHolder}
                    onSaveConfig={onSaveConfig}
                    isSaving={isSaving}
                    blendModes={blendModes}
                />
            )}
        </div>
    );
};

export default MainView;